$costColor: #D0021B;
$revenueColor: #7ED321;
$incomeColor: #4A90E2;
$shoppingColor: #f5a623;
$bgColor: #f2f2f2;
$titleColor: #9b9b9b;
$addBtnColor: #757575;
$newProColor: #ebebeb;

.checkAndEdit {
    .selectStatus {
        position: relative;
        margin-top: 18px;
        margin-left: 21px;
        input {
            position: absolute;
            top: 11px; }
        .fa-tags {
            position: absolute;
            top: 9px;
            left: 45px; }
        .btn {
            background-color: transparent;
            position: relative;
            &:focus {
                box-shadow: none; }
            &::after {
                position: absolute;
                top: 13px;
                right: -5px;
                border-top: .4em solid; } }
        .dropdown-menu {
            top: 20px !important;
            min-width: 120px;
            box-shadow: 1px 2px 10px 0 #9B9B9B;
            border-radius: 2px;
            .dropdown-item {
                font-weight: normal;
                text-align: center;
                font-size: 16px;
                padding: 8px 20px;
                transition: .3s all;
                &:hover {
                    background-color: black;
                    color: white; } } } }
    .changeStatusBtn {
        .btn {
            background-color: transparent;
            position: relative;
            margin-top: 20px;
            &:focus {
                box-shadow: none; }
            &::after {
                display: none; } }
        .dropdown-menu {
            min-width: 120px;
            box-shadow: 1px 2px 10px 0 #9B9B9B;
            border-radius: 2px;
            .dropdown-item {
                font-weight: normal;
                text-align: center;
                font-size: 16px;
                padding: 8px 20px;
                transition: .3s all;
                &:hover {
                    background-color: black;
                    color: white; } } } }
    .editMenu {
        position: relative;
        .btn {
            background-color: transparent;
            position: relative;
            font-weight: bold;
            font-size: 14px;
            margin-top: 18px;
            &:focus {
                box-shadow: none; }
            i {
                padding-left: 5px; } }
        .active {
            display: block !important; }
        .inputList {
            display: none;
            background: #FFFFFF;
            box-shadow: 1px 2px 10px 0 #9B9B9B;
            border-radius: 2px;
            position: absolute;
            top: 53px;
            z-index: 1000;
            width: 150px;
            .form-check {
                padding: 16px 32px;
                position: relative;
                input {
                    position: absolute;
                    top: 12px;
                    left: 32px; } } } } }
.tableSetting {
    table {
        width: 100%;
        .done {
            text-decoration: line-through; }
        .gray {
            color: $titleColor; }
        tr {
            &:nth-child(even) {
                background-color: $newProColor; }
            th {
                padding: 12px 0;
                font-weight: bold;
                font-size: 16px;
                background-color: #fff;
                padding-left: 40px; }
            td {
                font-size: 14px;
                line-height: 21px;
                padding-left: 40px;
                padding-top: 17px;
                position: relative;
                input {
                    position: absolute;
                    left: 20px;
                    top: 27px; }
                .price {
                    color: $titleColor; }
                .statusBtn {
                    font-size: 14px;
                    font-weight: bold;
                    color: #fff;
                    i {
                        padding-left: 5px; } }
                .btn {
                    background-color: transparent;
                    position: relative;
                    color: white;
                    font-size: 14px;
                    font-weight: bold;
                    &:focus {
                        box-shadow: none; }
                    &::after {
                        display: none; }
                    i {
                        padding-left: 5px; } }
                .dropdown-menu {
                    min-width: 120px;
                    box-shadow: 1px 2px 10px 0 #9B9B9B;
                    border-radius: 2px;
                    .dropdown-item {
                        width: 160px;
                        font-weight: normal;
                        text-align: center;
                        font-size: 16px;
                        padding: 8px 20px;
                        transition: .3s all;
                        &:hover {
                            background-color: black;
                            color: white; } } }
                .green {
                    background-color: $revenueColor; }
                .orange {
                    background-color: $shoppingColor; }
                .blue {
                    background-color: $incomeColor; }
                .gray {
                    background-color: $titleColor; }
                .productList {
                    &:last-child {
                        margin-bottom: 17px; } } } } } }
.pageBtn {
    width: 323px;
    height: 40px;
    border-radius: 4px;
    margin: 30px 0 40px 0;
    li {
        a {
            display: block;
            width: 41px;
            height: 41px;
            line-height: 41px;
            text-align: center;
            color: black;
            border: 1px solid #ECEEEF;
            background-color: white;
            text-decoration: none; }
        .active {
            background-color: black;
            color: white; } } }
