$costColor: #D0021B;
$revenueColor: #7ED321;
$incomeColor: #4A90E2;
$shoppingColor: #f5a623;
$bgColor: #f2f2f2;
$titleColor: #9b9b9b;
$addBtnColor: #757575;
$newProColor: #ebebeb;

.productSetting {
    .selectStatus {
        margin-top: 45px; }
    .changeStatusBtn {
        .btn {
            margin-top: 45px; } }
    .addProduct {
        font-size: 14px;
        color: #FFFFFF;
        font-weight: bold;
        background-color: black;
        padding: 0px 16px;
        height: 39px;
        margin-top: 30px;
        margin-bottom: 15px;
        i {
            padding-left: 5px; } } }
.ProductTableSetting {
    tbody {
        input {
            top: 34px !important; }
        .productImg {
            width: 50px;
            height: 50px;
            vertical-align: middle; }
        .Size-Color-Inventory {
            padding: 8px 0px;
            border-bottom: 1px solid white;
            &:nth-child(3) {
                border-bottom: none; }
            * > * {
                font-size: 14px; }
            .color {
                padding-right: 45px; }
            .inventory {
                padding-right: 10px; } } } }
.modal {
    .modal-dialog {
        top: 70px; }
    .modal-header {
        background-color: black;
        h5 {
            color: white;
            font-weight: bolder;
            font-size: 24px;
            padding-left: 20px; }
        .close {
            color: white;
            opacity: 1; } }
    .modal-body {
        padding: 30px;
        .upLoadImg {
            width: 395px;
            height: 115px;
            background-color: $newProColor;
            text-align: center;
            i {
                font-size: 39px;
                color: $addBtnColor;
                padding: 25px 0 8px 0; }
            h2 {
                font-size: 16px;
                color: $addBtnColor;
                line-height: 24px;
                font-weight: bolder; } }
        .upLoadInfo {
            margin-left: 30px;
            h2 {
                font-size: 16px;
                color: #373A3C;
                font-weight: bold;
                padding-bottom: 8px; }
            .titleInput {
                font-size: 16px;
                color: #999999;
                font-weight: normal; }
            textarea {
                border: 1px solid #CCCCCC;
                border-radius: 4px;
                padding: 5px;
                height: 170px;
                font-size: 16px;
                color: #999999;
                font-weight: normal;
                &::placeholder {
                    font-size: 16px;
                    color: #999999;
                    font-weight: normal; } }
            .TitleSetting {
                padding: 20px 0 8px 0;
                font-size: 16px;
                font-weight: bolder; }
            .originalAndDisCount {
                margin-bottom: 20px;
                & > * {
                    position: relative;
                    span {
                        position: absolute;
                        top: 1px;
                        left: 1px;
                        display: inline-block;
                        width: 87px;
                        height: 36px;
                        background-color: $newProColor;
                        border-radius: 4px 0 0 4px;
                        line-height: 36px;
                        text-align: center; }
                    input {
                        width: 187px;
                        height: 38px;
                        border: 1px solid #CCCCCC;
                        border-radius: 4px;
                        padding-left: 95px; } }
                .discount {
                    margin-left: 8px;
                    span {
                        border-left: 1px solid #cccccc;
                        left: 0; } } }
            .specification {
                & > * {
                    position: relative;
                    span {
                        position: absolute;
                        top: 1px;
                        left: 1px;
                        display: inline-block;
                        width: 71px;
                        height: 36px;
                        background-color: $newProColor;
                        border-radius: 4px 0 0 4px;
                        line-height: 36px;
                        text-align: center; }
                    input {
                        width: 130px;
                        height: 38px;
                        border: 1px solid #CCCCCC;
                        border-radius: 4px;
                        padding-left: 75px; }
                    select {
                        width: 65px;
                        height: 36px;
                        border: none;
                        position: absolute;
                        left: 57px;
                        padding-left: 20px; } }
                .size {
                    border: 1px solid #CCCCCC;
                    border-radius: 4px;
                    width: 125px;
                    span {
                        width: 60px;
                        left: -1px;
                        top: 0px;
                        border-left: 1px solid #cccccc; } }
                .color,.inventory {
                    margin-left: 8px; }
                .inventory {
                    span {
                        padding: 0 16px;
                        width: auto;
                        border-left: 1px solid #cccccc;
                        left: 0; }
                    input {
                        width: 160px;
                        padding-left: 103px; } } }
            .addBtn {
                display: block;
                background-color: black;
                color: white;
                text-decoration: none;
                text-align: center;
                font-size: 16px;
                font-weight: bolder;
                padding: 12px 0px;
                border-radius: 4px;
                border: 1px solid black;
                margin-top: 8px;
                i {
                    padding-left: 5px; } } } }
    .modal-footer {
        border-top: none;
        padding: 0;
        & > * {
            font-size: 16px;
            font-weight: bold;
            text-decoration: none;
            display: inline-block; }
        .save {
            color: $addBtnColor;
            margin: 0 30px 30px 0; }
        .publish {
            height: 34px;
            line-height: 34px;
            padding: 0 18px;
            color: white;
            background-color: black;
            border-radius: 4px;
            margin: 0 45px 30px 0; } } }
